import { Map, List } from 'immutable';
import { ADD_TODO } from './../app/input/inputActions';
import { COMPLETE_TODO, START_TODO } from './../app/todos/todosActions';
import { CLEAR_COMPLETED } from './../app/viewer/viewerActions';
import moment from 'moment';
import {v4} from 'node-uuid';
//import { REHYDRATE } from 'redux-persist/constants';

export const initialTodos = List([
  Map({
    id: v4(),
    created_at: "2016-12-23 11:00:00",
    text: 'Finish 6 Articles',
    complete_by: "2017-01-01 10:00:00",
    assigned_to: "@Kartika",
    working: false,
    complete: false,
    completed_at: ''
  }), Map({
    id: v4(),
    created_at: "2016-12-23 11:00:00",
    text: "Finish Runhr Kickstarter Expo",
    complete_by: "2017-01-01 10:00:00",
    assigned_to: "@Omotola",
    working: false,
    complete: false,
    completed_at: ''
  }), Map({
    id: v4(),
    created_at: "2016-12-23 11:00:00",
    text: 'Finish Runhr Press Release Package',
    complete_by: "2017-01-01 10:00:00",
    assigned_to: "@Szymon",
    working: false,
    complete: false,
    completed_at: ''
  }),
  Map({
    id: v4(),
    created_at: "2016-12-23 11:00:00",
    text: 'Start Runhr Kickstarter Campaign',
    complete_by: "2017-01-01 10:00:00",
    assigned_to: "@BosslessPress",
    working: false,
    complete: false,
    completed_at: ''
  })
]);


const todosReducer = (state = initialTodos, action) => {
  switch (action.type) {
    case ADD_TODO:
      return state.push(Map({
        id: v4(),
        created_at: moment().format().toString(),
        text: action.text,
        complete_by: action.complete_by,
        working: false,
        complete: false,
        completed_at: ''
      }));
    case START_TODO:
      return state.map(todo =>
        todo.get('id') === action.id ?
          todo.set('working', !todo.get('working')) :
          todo
      );
    case COMPLETE_TODO:
      return state.map(todo =>
        todo.get('id') === action.id ?
          todo.set('complete', !todo.get('complete')).set('completed_at', moment().format().toString()) :
          todo
        );
    case CLEAR_COMPLETED:
      return state.filter(todo => !todo.get('complete'));
    default:
      return state;
  }
};

export default todosReducer;



