import React, { Component, PropTypes } from 'react';
import { shouldComponentUpdate as shouldPureComponentUpdate } from 'react-addons-pure-render-mixin';
import {List,Button,Icon,Image,Item} from 'semantic-ui-react';
import moment from 'moment';


class Todo extends Component {
  constructor(props) {
    super(props);
    this.shouldComponentUpdate = shouldPureComponentUpdate.bind(this);
    this.startTodo = this.startTodo.bind(this);
    this.completeTodo = this.completeTodo.bind(this);
  }

  completeTodo() { this.props.completeTodo(this.props.id); }
  startTodo() { this.props.startTodo(this.props.id); }

  render() {
    const { text, completed_at, created_at, complete_by, assigned_to, working, complete } = this.props;
    return (

    <List.Item>

      <List.Content floated='right'>
        <Button onClick={this.startTodo}>Work</Button>
        <Button onClick={this.completeTodo}>Finish</Button>
      </List.Content>
      <List.Content>
        <List.Header>{text}</List.Header>
        <Item.Meta as='a'>{assigned_to}</Item.Meta>
        { complete ?
          <List.Description>Finished {moment(completed_at).fromNow()}</List.Description> :
          <List.Description>Deadline is {moment(complete_by).fromNow()}</List.Description>
        }

        { complete ?

          <Item.Extra>Finished {moment(completed_at).fromNow()} </Item.Extra> :
          <Item.Extra>Suggested {moment(created_at).fromNow()} </Item.Extra>
        }





      </List.Content>
    </List.Item>

    );
  }
}

Todo.propTypes = {
  id: PropTypes.string.isRequired,
  created_at: PropTypes.string,
  text: PropTypes.string.isRequired,
  complete_by: PropTypes.string,
  assigned_to: PropTypes.string,
  completed_at: PropTypes.string,
  working: PropTypes.bool.isRequired,
  complete: PropTypes.bool.isRequired,
  startTodo: PropTypes.func.isRequired,
  completeTodo: PropTypes.func.isRequired
};

export default Todo;



