import React, { PropTypes, Component } from 'react';
import { shouldComponentUpdate as shouldPureComponentUpdate } from 'react-addons-pure-render-mixin';
import { Segment, Button, Menu } from 'semantic-ui-react';
import Headroom from 'react-headroom'


class Viewer extends Component {
  constructor(props) {
    super(props);
    this.shouldComponentUpdate = shouldPureComponentUpdate.bind(this);
  }

  render() {
    const {
      showFilters,
      //numIncomplete,
      canClearCompleted,
      clearCompleted,
      showAll,
      showReady,
      showWorking,
      showCompleted,
      filterAll,
      filterReady,
      filterWorking,
      filterCompleted
    } = this.props;

  //  const numIncompleteText = numIncomplete === 1 ? 'item left' : 'items left';

    return showFilters ?

       <Headroom>
       <Menu fluid widths={5} secondary>
        <Menu.Item>Sprinter v0.1</Menu.Item>
        <Menu.Item name='All' active={showFilters === 'All'} onClick={filterAll} />
        <Menu.Item name='Ready' active={showFilters === 'Ready'} onClick={filterReady} />
        <Menu.Item name='Working' active={showFilters === 'Working'} onClick={filterWorking} />
        <Menu.Item name='Completed' active={showFilters === 'Completed'} onClick={filterCompleted} />
      </Menu>


{ /*
             canClearCompleted ?
                <Button
                  className='clear'
                  circular
            	  size='large'
                  onClick={clearCompleted}
                  icon="angle right"
                / > :
                null */}

	      </Headroom>


       : null;
  }
}

Viewer.propTypes = {
  showFilters: PropTypes.bool.isRequired,
  numIncomplete: PropTypes.number.isRequired,
  canClearCompleted: PropTypes.bool.isRequired,
  clearCompleted: PropTypes.func.isRequired,
  showAll: PropTypes.bool.isRequired,
  showReady: PropTypes.bool.isRequired,
  showWorking: PropTypes.bool.isRequired,
  showCompleted: PropTypes.bool.isRequired,
  filterAll: PropTypes.func.isRequired,
  filterReady: PropTypes.func.isRequired,
  filterWorking: PropTypes.func.isRequired,
  filterCompleted: PropTypes.func.isRequired
};

export default Viewer;



