export const ADD_TODO = 'ADD_TODO';


export const addTodo = (text,created_at, completed_at) => ({
  type: ADD_TODO,
  text,
  created_at,
  completed_at
});



export const APPLY_MODE = 'APPLY_MODE';

export const applyMode = mode => ({
  type: APPLY_MODE,
  mode
});



