import React, { PropTypes, Component } from 'react';
import ImmutablePropTypes from 'react-immutable-proptypes';
import { shouldComponentUpdate as shouldPureComponentUpdate } from 'react-addons-pure-render-mixin';
import { List, Header, Container } from 'semantic-ui-react'

import Todo from './Todo';
import InputItem from '../input/Input.container';

class Todos extends Component {
  constructor(props) {
    super(props);
    this.shouldComponentUpdate = shouldPureComponentUpdate.bind(this);
  }

  render() {
    const { todos, completeTodo, startTodo } = this.props;
    return (

      <List divided verticalAlign='middle'>


        {todos.map(todo => (
          <Todo
            key={todo.get('id')}
            id={todo.get('id')}
            created_at={todo.get('created_at')}
            text={todo.get('text')}
            complete_by={todo.get('complete_by')}
            assigned_to={todo.get('assigned_to')}
            working={todo.get('working')}
            complete={todo.get('complete')}
            completed_at={todo.get('completed_at')}
            completeTodo={completeTodo}
            startTodo={startTodo}
          />

        ))}
          <InputItem />
      </List>

    );
  }
}

Todos.propTypes = {
  todos: ImmutablePropTypes.list.isRequired,
  completeTodo: PropTypes.func.isRequired,
  startTodo: PropTypes.func.isRequired
};

export default Todos;



