import { connect } from 'react-redux';
import { clearCompleted, applyFilter } from './viewerActions';
import { ALL, READY, WORKING, COMPLETED } from './../../state/filterTypes';

import Viewer from './Viewer.view';

const mapStateToProps = state => ({
  showFilters: state.get('todos').size !== 0,
  canClearCompleted: state.get('todos').some(todo => todo.get('complete')),
  numIncomplete: state.get('todos')
    .reduce((count, todo) => !todo.get('complete') ? count + 1 : count, 0),
  showAll: state.get('filter') === ALL,
  showReady: state.get('filter') === READY,
  showWorking: state.get('filter') === WORKING,
  showCompleted: state.get('filter') === COMPLETED
});

const mapDispatchToProps = dispatch => ({
  clearCompleted: () => {
    dispatch(clearCompleted());
  },
  filterAll: () => {
    dispatch(applyFilter(ALL));
  },
  filterReady: () => {
    dispatch(applyFilter(READY));
  },
  filterWorking: () => {
    dispatch(applyFilter(WORKING));
  },
  filterCompleted: () => {
    dispatch(applyFilter(COMPLETED));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Viewer);



