import { connect } from 'react-redux';
import { completeTodo, startTodo } from './todosActions';
import Todos from './Todos.view';
import { ALL, READY, WORKING, COMPLETED } from './../../state/filterTypes';

const applyFilter = (todos, filter) => {
  switch (filter) {
    case ALL:
      return todos;
    case READY:
      return todos.filter(todo => !todo.get('working'));
    case WORKING:
      return todos.filter(todo => todo.get('working'));
    case COMPLETED:
      return todos.filter(todo => todo.get('complete'));
    default:
      return todos;
  }
};

const mapStateToProps = state => ({
  todos: applyFilter(state.get('todos'), state.get('filter'))
});

const mapDispatchToProps = dispatch => ({
  completeTodo: id => {
    dispatch(completeTodo(id));
  },
  startTodo: id => {
    dispatch(startTodo(id));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Todos);



