import { APPLY_FILTER } from './../app/viewer/viewerActions';
import { READY } from './filterTypes';
// import { REHYDRATE } from 'redux-persist/constants'
export const initialFilter = READY;

const filterReducer = (state = initialFilter, action) => {
  switch (action.type) {
    case APPLY_FILTER:
      return action.filter;
    default:
      return state;
  }
};

export default filterReducer;



